import React from 'react';
import PropTypes from 'prop-types';
import { Link, graphql } from 'gatsby';
import { css } from '@emotion/core';

import Bio from '../components/bio';
import Layout from '../components/layout';
import SEO from '../components/seo';
import { rhythm } from '../utils/typography';
import Image from 'gatsby-image';

const BlogIndex = ({ data, location }) => {
    const siteTitle = data.site.siteMetadata.title;
    const posts = data.allMarkdownRemark.edges;

    return (
        <Layout location={location} title={siteTitle}>
            <SEO title="All posts" />
            <Bio />
            <div css={css(containerStyle)}>
                {posts.map(({ node }) => {
                    const title = node.frontmatter.title || node.fields.slug;
                    const { cover } = node.frontmatter;
                    return (
                        <div css={css(itemContainerStyle)} key={node.fields.slug}>
                            <Link
                                style={{ color: 'inherit', boxShadow: `none` }}
                                to={node.fields.slug}
                            >
                                <div css={css(itemStyle)}>
                                    <div css={css(imgContainerStyle)}>
                                        {cover ? (
                                            <Image
                                                fixed={cover.childImageSharp.fixed}
                                                alt={title}
                                                css={css(imageStyle)}
                                            />
                                        ) : (
                                            <div css={css(imageStyle)}></div>
                                        )}
                                    </div>
                                    <div css={css(stringContainerSyle)}>
                                        <h3 css={css(titleStyle)}>{title}</h3>
                                        <small>{node.frontmatter.date}</small>
                                        <div>
                                            <p
                                                css={css(descStyle)}
                                                dangerouslySetInnerHTML={{
                                                    __html:
                                                        node.frontmatter.description ||
                                                        node.excerpt,
                                                }}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        </div>
                    );
                })}
            </div>
        </Layout>
    );
};

BlogIndex.propTypes = {
    data: PropTypes.object,
    location: PropTypes.object,
};

export default BlogIndex;

export const pageQuery = graphql`
    query {
        site {
            siteMetadata {
                title
            }
        }
        allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
            edges {
                node {
                    excerpt
                    fields {
                        slug
                    }
                    frontmatter {
                        date(formatString: "MMMM DD, YYYY")
                        title
                        description
                        cover {
                            childImageSharp {
                                fixed(width: 400, height: 300) {
                                    ...GatsbyImageSharpFixed
                                }
                            }
                        }
                    }
                }
            }
        }
    }
`;

const titleStyle = {
    marginTop: rhythm(1 / 4),
    marginBottom: rhythm(1 / 4),
};

const containerStyle = {
    display: 'flex',
    flexWrap: 'wrap',
    // mediaQuery('mobile')
};

const itemContainerStyle = {
    width: '33%',
    padding: '16px 8px',
    '@media only screen and (max-width: 768px)': {
        width: '100%',
    },
};

const itemStyle = {
    background: '#fbfbfb',
    maxWidth: '100%',
    overflow: 'hidden',
};

const imgContainerStyle = {
    overflow: 'hidden',
    width: '100%',
    minHeight: '300px',
    height: '300px',
};

const imageStyle = {
    width: '100%',
    minWidth: '100%',
    // marginRight: rhythm(1 / 2),
    // marginBottom: 0,
};

const stringContainerSyle = {
    padding: rhythm(1 / 4),
};

const descStyle = {
    marginBottom: rhythm(1 / 4),
};
